<template>
  <div class="menu-carousel" ref="carouselSection">
    <h2 class="menu-title">精選菜單</h2>
    <div class="menu-board">
      <h3>UPCOMING BREADS</h3>
      <img src="img/animated/bread-2.png" alt="裝飾麵包1" class="deco-bread1">
      <img src="img/animated/bread-3.png" alt="裝飾麵包2" class="deco-bread2">
      <div class="new-breads">
        <div class="new-bread">
          <div class="bread-des">
            <h5>100%全麥酸種麵包</h5>
            <p>使用天然酵母、麵粉、水與鹽，長時間低溫發酵，讓每一口酸種都帶有自然酸香、濕潤有嚼勁的口感</p>
          </div>
          <img src="img/menu/new-bread-1.png" alt="" class="new-product">
        </div>
        <div class="new-bread">
          <div class="bread-des">
            <h5>紅酒無花果硬歐麵包</h5>
            <p>選用天然酸種低溫發酵，揉入浸漬紅酒的土耳其無花果與核桃，外皮酥脆、內裡濕潤富彈性，果香與酒香交織出層次豐富的風味，適合搭配起司或單吃細品。</p>
          </div>
          <img src="img/menu/new-bread-2.png" alt="" class="new-product">
        </div>
        <div class="new-bread">
          <div class="bread-des">
            <h5>德國鹼水麵包</h5>
            <p>傳承德國經典工法，麵團經過鹼水浸泡後烘烤出獨特的深琥珀色外皮，帶有微微鹹香與酥脆口感，內裡柔軟有彈性。無論單吃、夾餡或搭配湯品都是絕佳選擇。</p>
          </div>
          <img src="img/menu/new-bread-3.png" alt="" class="new-product">
        </div>
      </div>
    </div>
    <div class="carousel-container">
      <button class="carousel-btn left" @click="prevSlide">‹</button>
      <div
        class="carousel-track"
        v-for="(item) in visibleItems"
        :key="item.id"
      >
          <div class="card">
            <img :src="item.image" class="card-img" alt="菜單圖片" />
            <div class="card-body">
              <h5 class="card-title">
                <router-link class="title-link" :to="'/menu/' + item.id">
                  {{ item.name }}
                </router-link>
              </h5>
              <p class="card-text">價格: ${{ item.price }}</p>
            </div>
          </div>
      </div>
      <button class="carousel-btn right" @click="nextSlide">›</button>
    </div>
    <div class="more-area">
      <router-link :to="'/menu'" class="more-btn">查看更多 <img src="img/icon/right-side.png" alt="前往更多"></router-link>
    </div>
  </div>
</template>

<script>
import { ref, computed, inject, onMounted, onUnmounted } from 'vue';

export default {
  setup() {
    const menuData = inject('menuData') || ref([]);
    const currentIndex = ref(0);
    const itemsPerPage = ref(1);
    const carouselSection = ref(null);
    const hasAnimated = ref(false);

    // **計算目前應該顯示哪些菜單**
    const visibleItems = computed(() => {
      return menuData.value.slice(currentIndex.value, currentIndex.value + itemsPerPage.value);
    });

    // **切換到下一組**
    const nextSlide = () => {
      if (currentIndex.value + itemsPerPage.value < menuData.value.length) {
        currentIndex.value += itemsPerPage.value;
      } else {
        currentIndex.value = 0; // 重新回到第一組
      }
    };

    // **切換到上一組**
    const prevSlide = () => {
      if (currentIndex.value - itemsPerPage.value >= 0) {
        currentIndex.value -= itemsPerPage.value;
      } else {
        currentIndex.value = menuData.value.length - itemsPerPage.value;
      }
    };

    // **監聽視窗大小，動態改變 itemsPerPage**
    const updateItemsPerPage = () => {
      const width = window.innerWidth;
      if (width > 1024) {
        itemsPerPage.value = 3; // 桌機：3 個
      } 
      else if (width > 768) {
        itemsPerPage.value = 2; // 平板：2 個
      } else {
        itemsPerPage.value = 1; // 手機：1 個
      }
    };

    // 確認可視範圍是否觸及菜單區域
    const handleScroll = () => {
      if (!carouselSection.value || hasAnimated.value) return;

      const rect = carouselSection.value.getBoundingClientRect();
      const windowHeight = window.innerHeight;
      const triggerPoint = 100;

      if (rect.top < windowHeight - triggerPoint) {
        carouselSection.value.classList.add('fade-in');
        hasAnimated.value = true;
      }
    };

    let interval = null;

    onMounted(() => {
      updateItemsPerPage(); // 初次設定
      window.addEventListener('resize', updateItemsPerPage);
      window.addEventListener('scroll', handleScroll); // 新增滾動事件監聽
      interval = setInterval(nextSlide, 3000); // 自動輪播
    });

    onUnmounted(() => {
      window.removeEventListener('resize', updateItemsPerPage);
      window.removeEventListener('scroll', handleScroll); // 移除滾動事件監聽
      clearInterval(interval);
    });

    return { visibleItems, nextSlide, prevSlide, carouselSection };
  },
};
</script>