<template>
  <div class="product-detail">
    <div v-if="product" class="product-info">
      <div class="info-img">
        <img :src="product.image" class="product-img" />
      </div>
      <div class="info-des">
        <h1>{{ product.name }}</h1>
        <p>{{ product.description }}</p>
        <p><span>${{ product.price }}</span> NTD / 個</p>
        <div class="btn-group">
          <button class="btn-primary" @click="addToCart(product)">加入購物車</button>
          <button @click="$router.push('/menu')" class="btn-secondary">返回菜單</button>
        </div>
      </div>
    </div>

    <!-- 其他產品區塊 -->
    <div v-if="relatedProducts.length > 0" class="related-products">
      <h2>其他推薦商品</h2>
      <div class="info-area">
        <div class="product-gallery">
          <div 
            v-for="(relatedProduct, index) in relatedProducts" 
            :key="index" 
            class="product-thumbnail"
          >
            <router-link :to="'/menu/' + relatedProduct.id" class="related-link">
              <img :src="relatedProduct.image" :alt="relatedProduct.name" />
              <p>{{ relatedProduct.name }}</p>
            </router-link>
          </div>
        </div>
      </div>
    </div>

    <div v-else>
      <p>找不到該商品</p>
    </div>
  </div>
</template>

<script>
import { ref, inject, computed, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';

export default {
  setup() {
    const route = useRoute();
    const store = useStore();
    const menuData = inject('menuData') || ref([]);
    const product = computed(() => menuData.value.find(item => item.id == route.params.id));
    const currentPage = ref(0);

    // 判斷螢幕大小的變數
    const isMobile = ref(false);

    // 隨機選擇產品函數
    const getRandomProducts = (arr, count) => {
      const shuffled = arr.slice();
      for (let i = shuffled.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
      }
      return shuffled.slice(0, count);
    };

    // 根據螢幕大小調整顯示的商品數量
    const relatedProducts = computed(() => {
      if (product.value) {
        const filteredProducts = menuData.value.filter(item => item.id !== product.value.id);
        return getRandomProducts(filteredProducts, isMobile.value ? 1 : 3); // 小螢幕顯示 1 個商品，其他顯示 3 個
      }
      return [];
    });

    // 判斷螢幕大小
    onMounted(() => {
      isMobile.value = window.innerWidth <= 768; // 判斷是否是小螢幕
      window.addEventListener('resize', handleResize);
    });

    const handleResize = () => {
      isMobile.value = window.innerWidth <= 768; // 更新螢幕大小判斷
    };

    // 加入購物車，使用 Vuex 的 mutation
    const addToCart = (item) => store.commit('addToCart', item);

    return { product, addToCart, relatedProducts, menuData, currentPage, isMobile };
  }
};
</script>
